import React, { useState, useRef } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { IconButton, Typography, Toolbar, AppBar, Drawer, Divider, CssBaseline, List, ListItem, ListItemIcon, ListItemText, Container} from '@material-ui/core/';
import { Menu as MenuIcon, ChevronLeft as ChevronLeftIcon, Person as PersonIcon, School as SchoolIcon, BusinessCenter as BusinessCentarIcon, FolderSpecial as FolderSpecialIcon, Star as StarIcon, Phone as PhoneIcon } from '@material-ui/icons/';
import clsx from 'clsx';
import Presentation from './Presentation';
import Education from './Education';
import Experience from './Experience';
import Portfolio from './Portfolio';
import Skills from './Skills';
import Contact from './Contact';

const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
    root: {
      display: 'flex',
    },
    toolbar: {
      paddingRight: 24, // keep right padding when drawer closed
    },
    toolbarIcon: {
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'flex-end',
      padding: '0 8px',
      ...theme.mixins.toolbar,
    },
    appBar: {
      zIndex: theme.zIndex.drawer + 1,
      transition: theme.transitions.create(['width', 'margin'], {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
      }),
    },
    appBarShift: {
      marginLeft: drawerWidth,
      width: `calc(100% - ${drawerWidth}px)`,
      transition: theme.transitions.create(['width', 'margin'], {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.enteringScreen,
      }),
    },
    menuButton: {
      marginRight: 36,
    },
    menuButtonHidden: {
      display: 'none',
    },
    title: {
      flexGrow: 1,
    },
    drawerPaper: {
      position: 'relative',
      whiteSpace: 'nowrap',
      width: drawerWidth,
      transition: theme.transitions.create('width', {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.enteringScreen,
      }),
    },
    drawerPaperClose: {
      overflowX: 'hidden',
      transition: theme.transitions.create('width', {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
      }),
      width: theme.spacing(7),
      [theme.breakpoints.up('sm')]: {
        width: theme.spacing(9),
      },
    },
    appBarSpacer: theme.mixins.toolbar,
    content: {
      flexGrow: 1,
      height: '100vh',
      overflow: 'auto',
    },
    container: {
      paddingTop: theme.spacing(4),
      paddingBottom: theme.spacing(4),
    },
    paper: {
      padding: theme.spacing(2),
      display: 'flex',
      overflow: 'auto',
      flexDirection: 'column',
    },
    fixedHeight: {
      height: 240,
    },
    options: {
        position: 'relative'
    },
    section: {
      paddingTop: '50px'
    }
  }));
  
 
const Main: React.FC = () => {
    const classes = useStyles();
    const fixedHeightPaper = clsx(classes.paper, classes.fixedHeight);
    const [open, setOpen] = useState(false);

    const presentationRef = useRef<HTMLDivElement>(null);
    const educationRef = useRef<HTMLDivElement>(null);
    const experienceRef = useRef<HTMLDivElement>(null);
    const portfolioRef = useRef<HTMLDivElement>(null);
    const skillsRef = useRef<HTMLDivElement>(null);
    const contactRef = useRef<HTMLDivElement>(null);

    const scroll = (reference: React.RefObject<HTMLDivElement>)  => {
      reference.current?.scrollIntoView({behavior: 'smooth'});
    }

    const handleNavBarOpen = () => {
        setOpen(true);
    }

    const handleNavBarClose = () => {
        setOpen(false);
    }

    
    return ( 
        <div className={classes.root}>
            <CssBaseline />
            <AppBar position="absolute" className={clsx(classes.appBar, open && classes.appBarShift)}>
                <Toolbar>
                <IconButton
                    edge="start"
                    color="inherit"
                    aria-label="open drawer"
                    onClick={handleNavBarOpen}
                    className={clsx(classes.menuButton, open && classes.menuButtonHidden)}
                >
                    <MenuIcon />
                </IconButton>
                <Typography variant="h6" className={classes.title}>
                    David Julián Spiess
                </Typography>
                
                </Toolbar>
            </AppBar>
            <Drawer
                variant="permanent"
                classes={{
                paper: clsx(classes.drawerPaper, !open && classes.drawerPaperClose),
                }}
                open={open}
            >
                <div className={classes.toolbarIcon}>
                <IconButton onClick={handleNavBarClose}>
                    <ChevronLeftIcon />
                </IconButton>
                </div>
                <Divider/>
                <List className={classes.options}>

                    <ListItem button onClick={() => scroll(presentationRef)}>
                            <ListItemIcon>
                                <PersonIcon />
                            </ListItemIcon>
                        <ListItemText primary="Presentation" />
                    </ListItem>
                  
                    <ListItem button onClick={() => scroll(educationRef)}>
                            <ListItemIcon>
                                <SchoolIcon />
                            </ListItemIcon>
                        <ListItemText primary="Education" />
                    </ListItem>
                    <ListItem button onClick={() => scroll(experienceRef)}>
                            <ListItemIcon>
                                <BusinessCentarIcon />
                            </ListItemIcon>
                        <ListItemText primary="Experience" />
                    </ListItem>
                    <ListItem button onClick={() => scroll(portfolioRef)}>
                            <ListItemIcon>
                                <FolderSpecialIcon />
                            </ListItemIcon>
                        <ListItemText primary="Portfolio" />
                    </ListItem>
                    <ListItem button onClick={() => scroll(skillsRef)}>
                            <ListItemIcon>
                                <StarIcon />
                            </ListItemIcon>
                        <ListItemText primary="Skills" />
                    </ListItem>
                    <ListItem button onClick={() => scroll(contactRef)}>
                            <ListItemIcon>
                                <PhoneIcon />
                            </ListItemIcon>
                        <ListItemText primary="Contact" />
                    </ListItem>
                </List>

            </Drawer>
            <main className={classes.content}>
              <div className={classes.appBarSpacer} />
              <Container maxWidth="lg" className={classes.container}>
                <div ref={presentationRef} className={classes.section}>
                  <Presentation/>
                </div>
                <Divider/>
                <div ref={educationRef} className={classes.section}>
                  <Education/>
                </div>
                <Divider/>
                <div ref={experienceRef} className={classes.section}>
                  <Experience/>
                </div>
                <Divider/>
                <div ref={portfolioRef} className={classes.section}>
                  <Portfolio/>
                </div>
                <Divider/>
                <div ref={skillsRef} className={classes.section}>
                  <Skills/>
                </div>
                <Divider/>
                <div ref={contactRef} className={classes.section}>
                  <Contact/>
                </div>
              </Container>
            </main>
        </div>
    );
}
 
export default Main;