import React from 'react';
import { List, ListItem, ListItemIcon, ListItemText } from '@material-ui/core/';
import LanguageIcon from '@material-ui/icons/Language';

 
const Porfolio: React.FC = () => {

    return ( 
        <div id="Portfolio">
            <h1>Portfolio</h1>
            <List>
                <ListItem button component="a" href="https://www.syersy.com">
                    <ListItemIcon>
                        <LanguageIcon/>
                    </ListItemIcon>
                    <ListItemText  primary="syersy.com" secondary="Web Site of self company: Syersy"/>
                </ListItem>
                <ListItem button component="a" href="https://sorting-spiess.syersy.com">
                    <ListItemIcon>
                        <LanguageIcon/>
                    </ListItemIcon>
                    <ListItemText  primary="Sorting Algorithms" secondary="A personal website to compare complexity between differents sorting algorithms (React.js Typescript - Node.js)"/>
                </ListItem>
            </List>
        </div>
     );
}
 
export default Porfolio;